.promo {
  font-family: @accent-font;
  text-align: center;
  color: @white;
  padding: 27px 20px 0;
  --cat-size: 302px;
  background-color: @accent-color;
  background-image: linear-gradient(@accent-color var(--cat-size), #ffffff var(--cat-size));
  position: relative;
  z-index: 0;

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: var(--cat-size);
    background-image: url("../img/index-background.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    opacity: 0.15;
    top: 0;
    left: 0;
    z-index: -1;
  }
}

.promo__title {
  font-size: 36px;
  font-weight: 400;
  line-height: 1;
  margin: 0 0 25px;
}

.promo__subtitle {
  line-height: 1;
  text-transform: uppercase;
}

@media (min-width: @tablet-width) {
  .promo {
    background-color: @white;
    background-image: none;
    color: @black;

    &::after {
      background-image: none;
    }
  }
}

@media (min-width: @desktop-width) {
  .promo {
    --cat-size: 694px;
    background-color: @accent-color;
    background-image: linear-gradient(@accent-color var(--cat-size), #ffffff var(--cat-size));

    &::after {
      background-image: url("../img/index-background.png");
    }
  }
}
